'use client'
import React from 'react'
import HeaderProductos from '../../components/productos/HeaderProductos'
import FiltrosProductos from '../../components/productos/FiltrosProductos'
import TablaProductos from '../../components/productos/TablaProductos'
import FormularioNuevoProductoModal from '../../components/productos/FormularioNuevoProductoModal' // Corrige el nombre si es necesario
import '@/styles/productos.css'
import {
  obtenerProductos,
  crearProducto,
  actualizarProducto,
  eliminarProducto
} from '@/features/productos/api'
import { Producto } from '@/shared/types/product.types'

export default function GestionDeProductosPage() {
  const [productos, setProductos] = React.useState<Producto[]>([])
  const [mostrarModal, setMostrarModal] = React.useState(false)
  const [productoEnEdicion, setProductoEnEdicion] = React.useState<any | null>(null)
  const [cargando, setCargando] = React.useState(true)
  const [error, setError] = React.useState<string | null>(null)
  const [filtros, setFiltros] = React.useState({
    texto: '',
    stock: 'Todos'
  })

  // Mueve cargarProductos aquí
  const cargarProductos = async () => {
    try {
      setCargando(true)
      setError(null)
      const data = await obtenerProductos()
      setProductos(data)
    } catch (err) {
      console.error('Error cargando productos:', err)
      setError('No se pudieron cargar los productos. Inténtalo de nuevo más tarde.')
    } finally {
      setCargando(false)
    }
  }

  React.useEffect(() => {
    cargarProductos()
  }, [])

  const agregarOEditarProducto = async (producto: any) => {
    try {
      const productoParaEnviar = {
        nombre: producto.nombre,
        descripcion: producto.descripcion,
        precio: parseFloat(producto.precio),
        stock: parseInt(producto.stock),
        categoria: producto.categoria,
      };

      if (producto.idProducto) {
        const actualizado = await actualizarProducto(producto.idProducto, productoParaEnviar);
        setProductos((prev) =>
          prev.map((p) => (p.idProducto === actualizado.idProducto ? actualizado : p))
        );
      } else {
        const nuevo = await crearProducto(productoParaEnviar);
        setProductos((prev) => [...prev, nuevo]);
      }
      setError(null);
    } catch (err) {
      console.error('Error al guardar producto:', err);
      setError('No se pudo guardar el producto. Asegúrate de que el servidor esté funcionando y los datos sean válidos.');
    }
  }

  const handleEliminar = async (id: number) => {
    const confirmar = window.confirm('¿Estás seguro de eliminar este producto?')
    if (confirmar) {
      try {
        await eliminarProducto(id)
        setProductos((prev) => prev.filter((p) => p.idProducto !== id))
      } catch (err) {
        console.error('Error eliminando producto:', err)
      }
    }
  }

  const handleEditar = (producto: any) => {
    setProductoEnEdicion(producto)
    setMostrarModal(true)
  }

  const handleNuevo = () => {
    setProductoEnEdicion(null)
    setMostrarModal(true)
  }

  const resetearFiltros = () => {
    setFiltros({
      texto: '',
      stock: 'Todos'
    });
    cargarProductos();
  };

  const handleExportar = () => {
    // Lógica para exportar productos (se implementará después de definir el formato)
    console.log('Exportar productos');
  };

  const filtrarProductos = () => {
    return productos.filter((p) => {
      const coincideTexto =
        filtros.texto === '' ||
        p.nombre.toLowerCase().includes(filtros.texto.toLowerCase())
      const coincideStock =
        filtros.stock === 'Todos' ||
        (filtros.stock === 'Alto' ? p.stock > 10 : p.stock < 10)
      return coincideTexto && coincideStock
    })
  }

  const productosFiltrados = filtrarProductos()

  return (
    <div className="dashboard-container">
      <HeaderProductos
        total={productosFiltrados.length}
        onNuevoProductoClick={handleNuevo}
      />
      <FiltrosProductos
        onChangeFiltros={setFiltros}
        onResetFiltros={resetearFiltros}
      />
      {cargando && <p>Cargando productos...</p>}
      {error && <p className="error-message">{error}</p>}
      {!cargando && !error && (
        <TablaProductos
          productos={productosFiltrados}
          onEditar={handleEditar}
          onEliminar={handleEliminar}
        />
      )}
      {mostrarModal && (
        <FormularioNuevoProductoModal
          onAgregar={agregarOEditarProducto}
          onCerrar={() => setMostrarModal(false)}
          productoAEditar={productoEnEdicion}
        />
      )}
    </div>
  )
}
